import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Button } from './ui/button';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { Checkbox } from './ui/checkbox';
import { ScrollArea } from './ui/scroll-area';
import { Cpu, Plus, Zap } from 'lucide-react';
import { toast } from '../hooks/use-toast';

const DeviceReference = () => {
  const [selectedDevices, setSelectedDevices] = useState([]);
  const [customVoltage, setCustomVoltage] = useState('5');
  const [totalPower, setTotalPower] = useState(null);

  const devices = {
    microcontrollers: [
      { name: 'Arduino Uno', voltage: 5, current_idle: 50, current_active: 200, unit: 'mA' },
      { name: 'Arduino Nano', voltage: 5, current_idle: 19, current_active: 80, unit: 'mA' },
      { name: 'Arduino Mega 2560', voltage: 5, current_idle: 70, current_active: 300, unit: 'mA' },
      { name: 'Arduino Pro Mini (3.3V)', voltage: 3.3, current_idle: 5, current_active: 25, unit: 'mA' },
      { name: 'Arduino Pro Mini (5V)', voltage: 5, current_idle: 8, current_active: 30, unit: 'mA' },
    ],
    esp: [
      { name: 'ESP8266 (idle)', voltage: 3.3, current_idle: 15, current_active: 80, unit: 'mA' },
      { name: 'ESP8266 (WiFi active)', voltage: 3.3, current_idle: 80, current_active: 170, unit: 'mA' },
      { name: 'ESP32 (idle)', voltage: 3.3, current_idle: 20, current_active: 80, unit: 'mA' },
      { name: 'ESP32 (WiFi active)', voltage: 3.3, current_idle: 80, current_active: 240, unit: 'mA' },
      { name: 'ESP32-S3', voltage: 3.3, current_idle: 25, current_active: 200, unit: 'mA' },
      { name: 'ESP32-C3', voltage: 3.3, current_idle: 15, current_active: 120, unit: 'mA' },
    ],
    raspberry: [
      { name: 'Raspberry Pi Zero W', voltage: 5, current_idle: 100, current_active: 150, unit: 'mA' },
      { name: 'Raspberry Pi Zero 2 W', voltage: 5, current_idle: 150, current_active: 350, unit: 'mA' },
      { name: 'Raspberry Pi 3 Model B+', voltage: 5, current_idle: 500, current_active: 1200, unit: 'mA' },
      { name: 'Raspberry Pi 4 (2GB)', voltage: 5, current_idle: 600, current_active: 1250, unit: 'mA' },
      { name: 'Raspberry Pi 4 (4GB)', voltage: 5, current_idle: 600, current_active: 1400, unit: 'mA' },
      { name: 'Raspberry Pi 5', voltage: 5, current_idle: 800, current_active: 1600, unit: 'mA' },
      { name: 'Raspberry Pi Pico', voltage: 3.3, current_idle: 1.3, current_active: 25, unit: 'mA' },
      { name: 'Raspberry Pi Pico W', voltage: 3.3, current_idle: 1.5, current_active: 50, unit: 'mA' },
    ],
    other: [
      { name: 'STM32F103 (Blue Pill)', voltage: 3.3, current_idle: 10, current_active: 50, unit: 'mA' },
      { name: 'Teensy 4.0', voltage: 3.3, current_idle: 15, current_active: 100, unit: 'mA' },
      { name: 'NodeMCU', voltage: 3.3, current_idle: 20, current_active: 170, unit: 'mA' },
      { name: 'BBC micro:bit', voltage: 3.3, current_idle: 5, current_active: 18, unit: 'mA' },
    ],
    sensors: [
      { name: 'DHT11 (teplota/vlhkost)', voltage: 3.3, current_idle: 0.2, current_active: 2.5, unit: 'mA' },
      { name: 'DHT22 (teplota/vlhkost)', voltage: 3.3, current_idle: 0.5, current_active: 2.5, unit: 'mA' },
      { name: 'BMP280 (tlak)', voltage: 3.3, current_idle: 0.003, current_active: 0.7, unit: 'mA' },
      { name: 'MPU6050 (gyro/akcelerometr)', voltage: 3.3, current_idle: 0.5, current_active: 3.8, unit: 'mA' },
      { name: 'HC-SR04 (ultrazvuk)', voltage: 5, current_idle: 2, current_active: 15, unit: 'mA' },
      { name: 'PIR senzor pohybu', voltage: 5, current_idle: 0.05, current_active: 0.1, unit: 'mA' },
    ],
    displays: [
      { name: 'OLED 0.96" (SSD1306)', voltage: 3.3, current_idle: 8, current_active: 20, unit: 'mA' },
      { name: 'LCD 16x2 (bez podsvcen)', voltage: 5, current_idle: 1, current_active: 3, unit: 'mA' },
      { name: 'LCD 16x2 (s podsvcenm)', voltage: 5, current_idle: 30, current_active: 50, unit: 'mA' },
      { name: 'NeoPixel LED (1 ks)', voltage: 5, current_idle: 0, current_active: 60, unit: 'mA' },
      { name: 'LED pska (10 cm)', voltage: 5, current_idle: 0, current_active: 180, unit: 'mA' },
    ],
  };

  const allDevices = [
    ...devices.microcontrollers,
    ...devices.esp,
    ...devices.raspberry,
    ...devices.other,
    ...devices.sensors,
    ...devices.displays,
  ];

  const toggleDevice = (device) => {
    const exists = selectedDevices.find(d => d.name === device.name);
    if (exists) {
      setSelectedDevices(selectedDevices.filter(d => d.name !== device.name));
    } else {
      setSelectedDevices([...selectedDevices, { ...device, mode: 'active' }]);
    }
  };

  const toggleMode = (deviceName) => {
    setSelectedDevices(selectedDevices.map(d => 
      d.name === deviceName ? { ...d, mode: d.mode === 'active' ? 'idle' : 'active' } : d
    ));
  };

  const calculateTotal = () => {
    if (selectedDevices.length === 0) {
      toast({ title: 'dn zazen', description: 'Vyberte alespo jedno zazen.', variant: 'destructive' });
      return;
    }

    let totalCurrentMa = 0;
    let warnings = [];

    selectedDevices.forEach(device => {
      const current = device.mode === 'active' ? device.current_active : device.current_idle;
      totalCurrentMa += current;

      // Check voltage compatibility
      if (parseFloat(customVoltage) < device.voltage) {
        warnings.push(`${device.name} potebuje ${device.voltage}V, ale napt je nastaveno na ${customVoltage}V`);
      }
    });

    const totalCurrentA = totalCurrentMa / 1000;
    const voltage = parseFloat(customVoltage);
    const totalPowerW = voltage * totalCurrentA;

    setTotalPower({
      current_ma: totalCurrentMa.toFixed(2),
      current_a: totalCurrentA.toFixed(3),
      power_w: totalPowerW.toFixed(3),
      voltage: voltage,
      warnings: warnings,
    });

    toast({ 
      title: 'Vpoet dokonen', 
      description: `Celkov spoteba: ${totalCurrentMa.toFixed(0)} mA (${totalPowerW.toFixed(2)} W)` 
    });
  };

  const clearSelection = () => {
    setSelectedDevices([]);
    setTotalPower(null);
  };

  const DeviceList = ({ categoryDevices, title }) => (
    <div className="space-y-2">
      <h3 className="font-semibold text-sm text-muted-foreground mb-3">{title}</h3>
      {categoryDevices.map((device) => {
        const isSelected = selectedDevices.find(d => d.name === device.name);
        return (
          <div
            key={device.name}
            className={`flex items-center justify-between p-3 border rounded-lg hover:bg-accent transition-colors cursor-pointer ${
              isSelected ? 'bg-blue-50 border-blue-300' : ''
            }`}
            onClick={() => toggleDevice(device)}
          >
            <div className="flex items-center gap-3">
              <Checkbox checked={!!isSelected} onCheckedChange={() => toggleDevice(device)} />
              <div>
                <p className="font-medium text-sm">{device.name}</p>
                <p className="text-xs text-muted-foreground">
                  {device.voltage}V  Idle: {device.current_idle} {device.unit}  Active: {device.current_active} {device.unit}
                </p>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center gap-2">
          <Cpu className="h-5 w-5 text-purple-500" />
          <CardTitle>Referenn pruka zazen</CardTitle>
        </div>
        <CardDescription>Spoteba bnch mikrokontrolr, senzor a display</CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="reference" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="reference">Referenn tabulka</TabsTrigger>
            <TabsTrigger value="calculator">Kalkulaka napjen</TabsTrigger>
          </TabsList>

          <TabsContent value="reference" className="mt-4">
            <ScrollArea className="h-[500px] pr-4">
              <div className="space-y-6">
                <DeviceList categoryDevices={devices.raspberry} title="Raspberry Pi" />
                <DeviceList categoryDevices={devices.esp} title="ESP32 / ESP8266" />
                <DeviceList categoryDevices={devices.microcontrollers} title="Arduino" />
                <DeviceList categoryDevices={devices.sensors} title="Senzory" />
                <DeviceList categoryDevices={devices.displays} title="Displeje a LED" />
                <DeviceList categoryDevices={devices.other} title="Ostatn" />
              </div>
            </ScrollArea>
          </TabsContent>

          <TabsContent value="calculator" className="mt-4 space-y-4">
            <div className="space-y-2">
              <Label>Napjec napt (V)</Label>
              <Input
                type="number"
                value={customVoltage}
                onChange={(e) => setCustomVoltage(e.target.value)}
                placeholder="nap. 5"
              />
            </div>

            <div className="space-y-2">
              <Label>Vybran zazen ({selectedDevices.length})</Label>
              <ScrollArea className="h-[250px] border rounded-lg p-3">
                {selectedDevices.length === 0 ? (
                  <div className="text-center py-8 text-muted-foreground">
                    <p className="text-sm">dn zazen nevybrna</p>
                    <p className="text-xs mt-1">Pejdte na kartu "Referenn tabulka" a vyberte zazen</p>
                  </div>
                ) : (
                  <div className="space-y-2">
                    {selectedDevices.map((device) => (
                      <div key={device.name} className="flex items-center justify-between p-2 border rounded">
                        <div className="flex-1">
                          <p className="text-sm font-medium">{device.name}</p>
                          <p className="text-xs text-muted-foreground">
                            {device.voltage}V  {device.mode === 'active' ? device.current_active : device.current_idle} {device.unit}
                          </p>
                        </div>
                        <Button
                          size="sm"
                          variant={device.mode === 'active' ? 'default' : 'outline'}
                          onClick={() => toggleMode(device.name)}
                        >
                          {device.mode === 'active' ? 'Aktivn' : 'Idle'}
                        </Button>
                      </div>
                    ))}
                  </div>
                )}
              </ScrollArea>
            </div>

            <Button onClick={calculateTotal} className="w-full">
              <Zap className="h-4 w-4 mr-2" /> Vypotat celkovou spotebu
            </Button>

            {totalPower && (
              <div className="space-y-3">
                <div className="p-4 bg-gradient-to-r from-green-50 to-emerald-50 border border-green-200 rounded-lg space-y-2">
                  <p className="text-sm font-semibold text-green-900">Celkov spoteba:</p>
                  <div className="grid grid-cols-2 gap-2 text-sm">
                    <div>
                      <span className="text-muted-foreground">Napt:</span>
                      <span className="ml-2 font-medium">{totalPower.voltage} V</span>
                    </div>
                    <div>
                      <span className="text-muted-foreground">Proud:</span>
                      <span className="ml-2 font-medium">{totalPower.current_ma} mA</span>
                    </div>
                    <div>
                      <span className="text-muted-foreground">Proud:</span>
                      <span className="ml-2 font-medium">{totalPower.current_a} A</span>
                    </div>
                    <div>
                      <span className="text-muted-foreground">Vkon:</span>
                      <span className="ml-2 font-medium">{totalPower.power_w} W</span>
                    </div>
                  </div>
                </div>

                {totalPower.warnings.length > 0 && (
                  <div className="p-4 bg-red-50 border border-red-200 rounded-lg">
                    <p className="text-sm font-semibold text-red-900 mb-2"> Upozornn:</p>
                    {totalPower.warnings.map((warning, idx) => (
                      <p key={idx} className="text-xs text-red-800">{warning}</p>
                    ))}
                  </div>
                )}

                <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
                  <p className="text-xs font-semibold text-blue-900 mb-1"> Doporuen:</p>
                  <p className="text-xs text-blue-800">
                    Pro stabiln provoz doporuujeme zdroj s kapacitou alespo {(parseFloat(totalPower.current_a) * 1.3).toFixed(2)} A
                    (30% rezerva)
                  </p>
                </div>
              </div>
            )}

            <Button onClick={clearSelection} variant="outline" className="w-full">Vymazat vbr</Button>
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
};

export default DeviceReference;
