import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card';
import { ScrollArea } from './ui/scroll-area';
import { Button } from './ui/button';
import { History, Trash2 } from 'lucide-react';
import { mockCalculationHistory } from '../mock';

const HistoryPanel = () => {
  const [history, setHistory] = useState([]);

  useEffect(() => {
    // Load mock history
    setHistory(mockCalculationHistory);
  }, []);

  const clearHistory = () => {
    setHistory([]);
  };

  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    const now = new Date();
    const diffMs = now - date;
    const diffMins = Math.floor(diffMs / 60000);
    const diffHours = Math.floor(diffMs / 3600000);

    if (diffMins < 1) return 'Just now';
    if (diffMins < 60) return `${diffMins} min ago`;
    if (diffHours < 24) return `${diffHours} hr ago`;
    return date.toLocaleDateString();
  };

  const getTypeLabel = (type) => {
    const labels = {
      'ohms-law': 'Ohm\'s Law',
      'power': 'Power',
      'resistor-series': 'Resistor Series',
      'resistor-parallel': 'Resistor Parallel',
      'battery-runtime': 'Battery Runtime',
      'battery-config': 'Battery Config',
    };
    return labels[type] || type;
  };

  return (
    <Card className="w-full h-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-2">
            <History className="h-5 w-5 text-slate-500" />
            <CardTitle>Calculation History</CardTitle>
          </div>
          {history.length > 0 && (
            <Button
              variant="ghost"
              size="sm"
              onClick={clearHistory}
              className="transition-all duration-200 hover:text-red-600"
            >
              <Trash2 className="h-4 w-4" />
            </Button>
          )}
        </div>
        <CardDescription>{history.length} calculation{history.length !== 1 ? 's' : ''}</CardDescription>
      </CardHeader>
      <CardContent>
        <ScrollArea className="h-[400px] pr-4">
          {history.length === 0 ? (
            <div className="text-center py-8 text-muted-foreground">
              <History className="h-12 w-12 mx-auto mb-3 opacity-30" />
              <p>No calculations yet</p>
              <p className="text-sm mt-1">Your calculation history will appear here</p>
            </div>
          ) : (
            <div className="space-y-3">
              {history.map((item) => (
                <div
                  key={item.id}
                  className="p-3 border rounded-lg hover:bg-accent transition-colors duration-200"
                >
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <p className="text-sm font-medium text-primary">{getTypeLabel(item.type)}</p>
                      <p className="text-xs text-muted-foreground mt-1">{item.calculation}</p>
                      <div className="mt-2 text-xs space-y-1">
                        {Object.entries(item.result).map(([key, value]) => (
                          <p key={key} className="text-green-700 dark:text-green-400">
                            {key}: {typeof value === 'number' ? value.toFixed(4) : value}
                          </p>
                        ))}
                      </div>
                    </div>
                    <span className="text-xs text-muted-foreground">{formatTimestamp(item.timestamp)}</span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </ScrollArea>
      </CardContent>
    </Card>
  );
};

export default HistoryPanel;
