import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { Button } from './ui/button';
import { ArrowRightLeft } from 'lucide-react';
import { toast } from '../hooks/use-toast';

const UnitConverter = () => {
  const [category, setCategory] = useState('voltage');
  const [fromUnit, setFromUnit] = useState('');
  const [toUnit, setToUnit] = useState('');
  const [inputValue, setInputValue] = useState('');
  const [result, setResult] = useState(null);

  const conversions = {
    voltage: {
      units: ['V', 'mV', 'V', 'kV', 'MV'],
      factors: { V: 1, mV: 0.001, 'V': 0.000001, kV: 1000, MV: 1000000 },
    },
    current: {
      units: ['A', 'mA', 'A', 'nA', 'kA'],
      factors: { A: 1, mA: 0.001, 'A': 0.000001, nA: 0.000000001, kA: 1000 },
    },
    resistance: {
      units: ['', 'm', 'k', 'M', 'G'],
      factors: { '': 1, 'm': 0.001, 'k': 1000, 'M': 1000000, 'G': 1000000000 },
    },
    power: {
      units: ['W', 'mW', 'W', 'kW', 'MW', 'GW', 'hp'],
      factors: { W: 1, mW: 0.001, 'W': 0.000001, kW: 1000, MW: 1000000, GW: 1000000000, hp: 745.7 },
    },
    capacitance: {
      units: ['F', 'mF', 'F', 'nF', 'pF'],
      factors: { F: 1, mF: 0.001, 'F': 0.000001, nF: 0.000000001, pF: 0.000000000001 },
    },
    inductance: {
      units: ['H', 'mH', 'H', 'nH'],
      factors: { H: 1, mH: 0.001, 'H': 0.000001, nH: 0.000000001 },
    },
    charge: {
      units: ['C', 'mC', 'C', 'nC', 'pC', 'Ah', 'mAh'],
      factors: { C: 1, mC: 0.001, 'C': 0.000001, nC: 0.000000001, pC: 0.000000000001, Ah: 3600, mAh: 3.6 },
    },
    energy: {
      units: ['J', 'kJ', 'MJ', 'Wh', 'kWh', 'eV', 'cal', 'kcal'],
      factors: { J: 1, kJ: 1000, MJ: 1000000, Wh: 3600, kWh: 3600000, eV: 1.602e-19, cal: 4.184, kcal: 4184 },
    },
    frequency: {
      units: ['Hz', 'kHz', 'MHz', 'GHz', 'THz'],
      factors: { Hz: 1, kHz: 1000, MHz: 1000000, GHz: 1000000000, THz: 1000000000000 },
    },
    temperature: {
      units: ['C', 'K', 'F'],
      factors: null, // Special handling needed
    },
    resistivity: {
      units: ['m', 'cm', 'mm'],
      factors: { 'm': 1, 'cm': 0.01, 'mm': 0.001 },
    },
  };

  const convertTemperature = (value, from, to) => {
    // Convert to Celsius first
    let celsius;
    if (from === 'C') celsius = value;
    else if (from === 'K') celsius = value - 273.15;
    else if (from === 'F') celsius = (value - 32) * 5/9;

    // Convert from Celsius to target
    if (to === 'C') return celsius;
    else if (to === 'K') return celsius + 273.15;
    else if (to === 'F') return celsius * 9/5 + 32;
  };

  const convert = () => {
    if (!fromUnit || !toUnit || !inputValue) {
      toast({ title: 'Neplatn vstup', description: 'Vyplte vechna pole.', variant: 'destructive' });
      return;
    }

    const value = parseFloat(inputValue);
    if (isNaN(value)) {
      toast({ title: 'Neplatn vstup', description: 'Zadejte platn slo.', variant: 'destructive' });
      return;
    }

    let convertedValue;

    // Special handling for temperature
    if (category === 'temperature') {
      convertedValue = convertTemperature(value, fromUnit, toUnit);
    } else {
      // Convert to base unit first, then to target unit
      const baseValue = value * conversions[category].factors[fromUnit];
      convertedValue = baseValue / conversions[category].factors[toUnit];
    }

    setResult(convertedValue);
    toast({ 
      title: 'Pevod dokonen', 
      description: `${value} ${fromUnit} = ${convertedValue.toFixed(9)} ${toUnit}` 
    });
  };

  const swap = () => {
    const temp = fromUnit;
    setFromUnit(toUnit);
    setToUnit(temp);
    if (result) {
      setInputValue(result.toString());
      setResult(parseFloat(inputValue));
    }
  };

  const clear = () => {
    setInputValue('');
    setResult(null);
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center gap-2">
          <ArrowRightLeft className="h-5 w-5 text-indigo-500" />
          <CardTitle>Pevodnk jednotek</CardTitle>
        </div>
        <CardDescription>Pevod mezi rznmi elektrickmi a fyziklnmi jednotkami</CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="space-y-2">
          <Label>Kategorie</Label>
          <Select value={category} onValueChange={(val) => { setCategory(val); setFromUnit(''); setToUnit(''); setResult(null); }}>
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="voltage">Napt</SelectItem>
              <SelectItem value="current">Proud</SelectItem>
              <SelectItem value="resistance">Odpor</SelectItem>
              <SelectItem value="power">Vkon</SelectItem>
              <SelectItem value="capacitance">Kapacita</SelectItem>
              <SelectItem value="inductance">Induknost</SelectItem>
              <SelectItem value="charge">Elektrick nboj</SelectItem>
              <SelectItem value="energy">Energie</SelectItem>
              <SelectItem value="frequency">Frekvence</SelectItem>
              <SelectItem value="temperature">Teplota</SelectItem>
              <SelectItem value="resistivity">Resistivita</SelectItem>
            </SelectContent>
          </Select>
        </div>

        <div className="space-y-2">
          <Label>Z</Label>
          <div className="flex gap-2">
            <Input
              type="number"
              placeholder="Hodnota"
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              className="flex-1"
            />
            <Select value={fromUnit} onValueChange={setFromUnit}>
              <SelectTrigger className="w-32">
                <SelectValue placeholder="Jednotka" />
              </SelectTrigger>
              <SelectContent>
                {conversions[category].units.map((unit) => (
                  <SelectItem key={unit} value={unit}>{unit}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>

        <div className="flex justify-center">
          <Button
            variant="outline"
            size="icon"
            onClick={swap}
            className="transition-all duration-200 hover:rotate-180"
          >
            <ArrowRightLeft className="h-4 w-4" />
          </Button>
        </div>

        <div className="space-y-2">
          <Label>Na</Label>
          <div className="flex gap-2">
            <Input
              type="text"
              placeholder="Vsledek"
              value={result !== null ? result.toFixed(9) : ''}
              readOnly
              className="flex-1 bg-muted"
            />
            <Select value={toUnit} onValueChange={setToUnit}>
              <SelectTrigger className="w-32">
                <SelectValue placeholder="Jednotka" />
              </SelectTrigger>
              <SelectContent>
                {conversions[category].units.map((unit) => (
                  <SelectItem key={unit} value={unit}>{unit}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>

        <div className="flex gap-3">
          <Button onClick={convert} className="flex-1 transition-all duration-200 hover:scale-105">Pevst</Button>
          <Button onClick={clear} variant="outline" className="transition-all duration-200 hover:scale-105">Vymazat</Button>
        </div>

        <div className="p-3 bg-blue-50 border border-blue-200 rounded-lg">
          <p className="text-xs text-blue-700">
             Podporovan kategorie: Napt, Proud, Odpor, Vkon, Kapacita, Induknost, 
            Elektrick nboj, Energie, Frekvence, Teplota, Resistivita
          </p>
        </div>
      </CardContent>
    </Card>
  );
};

export default UnitConverter;
