import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Button } from './ui/button';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { Atom, Zap, Layers } from 'lucide-react';
import { toast } from '../hooks/use-toast';
import { saveCalculation } from '../mock';

const AdvancedCalculators = () => {
  // Electric Charge Calculator (Q = I  t, Q = C  V)
  const [chargeMode, setChargeMode] = useState('it');
  const [current, setCurrent] = useState('');
  const [time, setTime] = useState('');
  const [timeUnit, setTimeUnit] = useState('s');
  const [capacitance, setCapacitance] = useState('');
  const [voltage, setVoltage] = useState('');
  const [chargeResult, setChargeResult] = useState(null);

  // Current Calculator (I = Q/t, I = V/R, I = P/V)
  const [currentCalcMode, setCurrentCalcMode] = useState('vr');
  const [voltageI, setVoltageI] = useState('');
  const [resistanceI, setResistanceI] = useState('');
  const [charge, setCharge] = useState('');
  const [timeI, setTimeI] = useState('');
  const [power, setPower] = useState('');
  const [currentResult, setCurrentResult] = useState(null);

  // Resistivity Calculator ( = R  A / L)
  const [resistivityMode, setResistivityMode] = useState('calculate');
  const [resistance, setResistance] = useState('');
  const [area, setArea] = useState('');
  const [areaUnit, setAreaUnit] = useState('mm2');
  const [length, setLength] = useState('');
  const [lengthUnit, setLengthUnit] = useState('m');
  const [resistivity, setResistivity] = useState('');
  const [resistivityResult, setResistivityResult] = useState(null);

  // Material resistivity database
  const materials = [
    { name: 'Stbro (Ag)', resistivity: 1.59e-8, tempCoef: 0.0038 },
    { name: 'M (Cu)', resistivity: 1.68e-8, tempCoef: 0.00386 },
    { name: 'Zlato (Au)', resistivity: 2.44e-8, tempCoef: 0.0034 },
    { name: 'Hlink (Al)', resistivity: 2.65e-8, tempCoef: 0.0039 },
    { name: 'Wolfram (W)', resistivity: 5.60e-8, tempCoef: 0.0045 },
    { name: 'elezo (Fe)', resistivity: 9.70e-8, tempCoef: 0.005 },
    { name: 'Platina (Pt)', resistivity: 10.6e-8, tempCoef: 0.00377 },
    { name: 'Ocel', resistivity: 1.43e-7, tempCoef: 0.003 },
    { name: 'Nikl (Ni)', resistivity: 6.99e-8, tempCoef: 0.006 },
    { name: 'Konstantan', resistivity: 4.9e-7, tempCoef: 0.00001 },
    { name: 'Grafit', resistivity: 1.0e-5, tempCoef: -0.0005 },
  ];

  const calculateCharge = () => {
    if (chargeMode === 'it') {
      const i = parseFloat(current);
      const t = parseFloat(time);
      if (!i || !t) {
        toast({ title: 'Neplatn vstup', description: 'Zadejte proud a as.', variant: 'destructive' });
        return;
      }

      // Convert time to seconds
      const timeFactors = { s: 1, min: 60, h: 3600, ms: 0.001 };
      const timeInSeconds = t * timeFactors[timeUnit];
      
      const q = i * timeInSeconds; // Q in Coulombs
      setChargeResult({ charge: q, formula: 'Q = I  t' });
      toast({ title: 'Vpoet dokonen', description: `Nboj = ${q.toFixed(6)} C` });
    } else if (chargeMode === 'cv') {
      const c = parseFloat(capacitance);
      const v = parseFloat(voltage);
      if (!c || !v) {
        toast({ title: 'Neplatn vstup', description: 'Zadejte kapacitu a napt.', variant: 'destructive' });
        return;
      }

      const q = c * v; // Q in Coulombs (if C in F and V in V)
      setChargeResult({ charge: q, formula: 'Q = C  V' });
      toast({ title: 'Vpoet dokonen', description: `Nboj = ${q.toFixed(9)} C` });
    }
  };

  const calculateCurrent = () => {
    let result = 0;
    let formula = '';

    if (currentCalcMode === 'vr') {
      const v = parseFloat(voltageI);
      const r = parseFloat(resistanceI);
      if (!v || !r) {
        toast({ title: 'Neplatn vstup', description: 'Zadejte napt a odpor.', variant: 'destructive' });
        return;
      }
      result = v / r;
      formula = 'I = V / R';
    } else if (currentCalcMode === 'qt') {
      const q = parseFloat(charge);
      const t = parseFloat(timeI);
      if (!q || !t) {
        toast({ title: 'Neplatn vstup', description: 'Zadejte nboj a as.', variant: 'destructive' });
        return;
      }
      result = q / t;
      formula = 'I = Q / t';
    } else if (currentCalcMode === 'pv') {
      const p = parseFloat(power);
      const v = parseFloat(voltageI);
      if (!p || !v) {
        toast({ title: 'Neplatn vstup', description: 'Zadejte vkon a napt.', variant: 'destructive' });
        return;
      }
      result = p / v;
      formula = 'I = P / V';
    }

    setCurrentResult({ current: result, formula });
    toast({ title: 'Vpoet dokonen', description: `Proud = ${result.toFixed(6)} A` });
  };

  const calculateResistivity = () => {
    const r = parseFloat(resistance);
    const a = parseFloat(area);
    const l = parseFloat(length);

    if (!r || !a || !l) {
      toast({ title: 'Neplatn vstup', description: 'Zadejte odpor, prez a dlku.', variant: 'destructive' });
      return;
    }

    // Convert units to meters and square meters
    const areaFactors = { 'mm2': 1e-6, 'cm2': 1e-4, 'm2': 1 };
    const lengthFactors = { 'mm': 0.001, 'cm': 0.01, 'm': 1, 'km': 1000 };
    
    const areaM2 = a * areaFactors[areaUnit];
    const lengthM = l * lengthFactors[lengthUnit];

    //  = R  A / L
    const rho = (r * areaM2) / lengthM;
    
    // Find closest material
    let closestMaterial = null;
    let minDiff = Infinity;
    materials.forEach(mat => {
      const diff = Math.abs(Math.log10(mat.resistivity) - Math.log10(rho));
      if (diff < minDiff) {
        minDiff = diff;
        closestMaterial = mat;
      }
    });

    setResistivityResult({
      resistivity: rho,
      formula: ' = R  A / L',
      closestMaterial: minDiff < 0.5 ? closestMaterial : null,
    });

    toast({ title: 'Vpoet dokonen', description: `Resistivita = ${rho.toExponential(3)} m` });
  };

  const useMaterialResistivity = (material) => {
    setResistivity(material.resistivity.toExponential(3));
    toast({ title: 'Materil vybrn', description: `${material.name}: ${material.resistivity.toExponential(3)} m` });
  };

  const clearCharge = () => {
    setCurrent('');
    setTime('');
    setCapacitance('');
    setVoltage('');
    setChargeResult(null);
  };

  const clearCurrent = () => {
    setVoltageI('');
    setResistanceI('');
    setCharge('');
    setTimeI('');
    setPower('');
    setCurrentResult(null);
  };

  const clearResistivity = () => {
    setResistance('');
    setArea('');
    setLength('');
    setResistivity('');
    setResistivityResult(null);
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center gap-2">
          <Atom className="h-5 w-5 text-cyan-500" />
          <CardTitle>Pokroil elektrick vpoty</CardTitle>
        </div>
        <CardDescription>Elektrick nboj, proud a resistivita</CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="charge" className="w-full">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="charge">El. nboj (Q)</TabsTrigger>
            <TabsTrigger value="current">El. proud (I)</TabsTrigger>
            <TabsTrigger value="resistivity">Resistivita ()</TabsTrigger>
          </TabsList>

          {/* Electric Charge */}
          <TabsContent value="charge" className="space-y-4 mt-4">
            <div className="space-y-2">
              <Label>Vzorec</Label>
              <Select value={chargeMode} onValueChange={setChargeMode}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="it">Q = I  t (proud  as)</SelectItem>
                  <SelectItem value="cv">Q = C  V (kapacita  napt)</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {chargeMode === 'it' ? (
              <div className="space-y-4">
                <div className="space-y-2">
                  <Label>Proud (I) [A]</Label>
                  <Input type="number" value={current} onChange={(e) => setCurrent(e.target.value)} placeholder="Ampry" />
                </div>
                <div className="space-y-2">
                  <Label>as (t)</Label>
                  <div className="flex gap-2">
                    <Input type="number" value={time} onChange={(e) => setTime(e.target.value)} placeholder="as" className="flex-1" />
                    <Select value={timeUnit} onValueChange={setTimeUnit}>
                      <SelectTrigger className="w-24">
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="ms">ms</SelectItem>
                        <SelectItem value="s">s</SelectItem>
                        <SelectItem value="min">min</SelectItem>
                        <SelectItem value="h">h</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>
              </div>
            ) : (
              <div className="space-y-4">
                <div className="space-y-2">
                  <Label>Kapacita (C) [F]</Label>
                  <Input type="number" value={capacitance} onChange={(e) => setCapacitance(e.target.value)} placeholder="Farady" />
                </div>
                <div className="space-y-2">
                  <Label>Napt (V) [V]</Label>
                  <Input type="number" value={voltage} onChange={(e) => setVoltage(e.target.value)} placeholder="Volty" />
                </div>
              </div>
            )}

            <Button onClick={calculateCharge} className="w-full">Vypotat nboj</Button>

            {chargeResult && (
              <div className="p-4 bg-gradient-to-r from-cyan-50 to-blue-50 border border-cyan-200 rounded-lg">
                <p className="text-sm font-medium text-cyan-900">{chargeResult.formula}</p>
                <p className="text-lg font-bold text-cyan-700 mt-2">Q = {chargeResult.charge.toExponential(6)} C</p>
                <p className="text-xs text-cyan-600 mt-1">({(chargeResult.charge * 1e6).toFixed(3)} C)</p>
              </div>
            )}

            <Button onClick={clearCharge} variant="outline" className="w-full">Vymazat</Button>
          </TabsContent>

          {/* Electric Current */}
          <TabsContent value="current" className="space-y-4 mt-4">
            <div className="space-y-2">
              <Label>Vzorec</Label>
              <Select value={currentCalcMode} onValueChange={setCurrentCalcMode}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="vr">I = V / R (Ohmv zkon)</SelectItem>
                  <SelectItem value="qt">I = Q / t (nboj / as)</SelectItem>
                  <SelectItem value="pv">I = P / V (vkon / napt)</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {currentCalcMode === 'vr' && (
              <div className="space-y-4">
                <div className="space-y-2">
                  <Label>Napt (V) [V]</Label>
                  <Input type="number" value={voltageI} onChange={(e) => setVoltageI(e.target.value)} placeholder="Volty" />
                </div>
                <div className="space-y-2">
                  <Label>Odpor (R) []</Label>
                  <Input type="number" value={resistanceI} onChange={(e) => setResistanceI(e.target.value)} placeholder="Ohmy" />
                </div>
              </div>
            )}

            {currentCalcMode === 'qt' && (
              <div className="space-y-4">
                <div className="space-y-2">
                  <Label>Nboj (Q) [C]</Label>
                  <Input type="number" value={charge} onChange={(e) => setCharge(e.target.value)} placeholder="Coulomby" />
                </div>
                <div className="space-y-2">
                  <Label>as (t) [s]</Label>
                  <Input type="number" value={timeI} onChange={(e) => setTimeI(e.target.value)} placeholder="Sekundy" />
                </div>
              </div>
            )}

            {currentCalcMode === 'pv' && (
              <div className="space-y-4">
                <div className="space-y-2">
                  <Label>Vkon (P) [W]</Label>
                  <Input type="number" value={power} onChange={(e) => setPower(e.target.value)} placeholder="Watty" />
                </div>
                <div className="space-y-2">
                  <Label>Napt (V) [V]</Label>
                  <Input type="number" value={voltageI} onChange={(e) => setVoltageI(e.target.value)} placeholder="Volty" />
                </div>
              </div>
            )}

            <Button onClick={calculateCurrent} className="w-full">Vypotat proud</Button>

            {currentResult && (
              <div className="p-4 bg-gradient-to-r from-amber-50 to-yellow-50 border border-amber-200 rounded-lg">
                <p className="text-sm font-medium text-amber-900">{currentResult.formula}</p>
                <p className="text-lg font-bold text-amber-700 mt-2">I = {currentResult.current.toFixed(6)} A</p>
                <p className="text-xs text-amber-600 mt-1">({(currentResult.current * 1000).toFixed(3)} mA)</p>
              </div>
            )}

            <Button onClick={clearCurrent} variant="outline" className="w-full">Vymazat</Button>
          </TabsContent>

          {/* Resistivity */}
          <TabsContent value="resistivity" className="space-y-4 mt-4">
            <div className="space-y-4">
              <div className="p-3 bg-blue-50 border border-blue-200 rounded-lg">
                <p className="text-sm font-semibold text-blue-900 mb-2">Vzorec:  = R  A / L</p>
                <p className="text-xs text-blue-700"> - resistivita [m], R - odpor [], A - prez [m], L - dlka [m]</p>
              </div>

              <div className="space-y-2">
                <Label>Odpor (R) []</Label>
                <Input type="number" value={resistance} onChange={(e) => setResistance(e.target.value)} placeholder="Ohmy" />
              </div>

              <div className="space-y-2">
                <Label>Prez vodie (A)</Label>
                <div className="flex gap-2">
                  <Input type="number" value={area} onChange={(e) => setArea(e.target.value)} placeholder="Prez" className="flex-1" />
                  <Select value={areaUnit} onValueChange={setAreaUnit}>
                    <SelectTrigger className="w-24">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="mm2">mm</SelectItem>
                      <SelectItem value="cm2">cm</SelectItem>
                      <SelectItem value="m2">m</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div className="space-y-2">
                <Label>Dlka vodie (L)</Label>
                <div className="flex gap-2">
                  <Input type="number" value={length} onChange={(e) => setLength(e.target.value)} placeholder="Dlka" className="flex-1" />
                  <Select value={lengthUnit} onValueChange={setLengthUnit}>
                    <SelectTrigger className="w-24">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="mm">mm</SelectItem>
                      <SelectItem value="cm">cm</SelectItem>
                      <SelectItem value="m">m</SelectItem>
                      <SelectItem value="km">km</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </div>

            <Button onClick={calculateResistivity} className="w-full">Vypotat resistivitu</Button>

            {resistivityResult && (
              <div className="space-y-3">
                <div className="p-4 bg-gradient-to-r from-green-50 to-emerald-50 border border-green-200 rounded-lg">
                  <p className="text-sm font-medium text-green-900">{resistivityResult.formula}</p>
                  <p className="text-lg font-bold text-green-700 mt-2"> = {resistivityResult.resistivity.toExponential(3)} m</p>
                </div>
                {resistivityResult.closestMaterial && (
                  <div className="p-3 bg-purple-50 border border-purple-200 rounded-lg">
                    <p className="text-sm font-semibold text-purple-900"> Pravdpodobn materil:</p>
                    <p className="text-sm text-purple-700 mt-1">
                      {resistivityResult.closestMaterial.name} ( = {resistivityResult.closestMaterial.resistivity.toExponential(3)} m)
                    </p>
                  </div>
                )}
              </div>
            )}

            <div className="space-y-2">
              <Label>Databze materil</Label>
              <div className="border rounded-lg p-3 max-h-60 overflow-y-auto space-y-2">
                {materials.map((material, idx) => (
                  <div key={idx} className="flex items-center justify-between p-2 hover:bg-accent rounded transition-colors">
                    <div>
                      <p className="text-sm font-medium">{material.name}</p>
                      <p className="text-xs text-muted-foreground">{material.resistivity.toExponential(2)} m</p>
                    </div>
                    <Button size="sm" variant="outline" onClick={() => useMaterialResistivity(material)}>
                      Pout
                    </Button>
                  </div>
                ))}
              </div>
            </div>

            <Button onClick={clearResistivity} variant="outline" className="w-full">Vymazat</Button>
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
};

export default AdvancedCalculators;
