import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Button } from './ui/button';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { Cpu, Plus, Trash2 } from 'lucide-react';
import { toast } from '../hooks/use-toast';
import { saveCalculation, resistorColorCodes } from '../mock';

const ResistorCalculator = () => {
  const [resistors, setResistors] = useState(['', '']);
  const [result, setResult] = useState(null);
  
  // Color code state
  const [band1, setBand1] = useState('');
  const [band2, setBand2] = useState('');
  const [multiplier, setMultiplier] = useState('');
  const [tolerance, setTolerance] = useState('');
  const [colorCodeResult, setColorCodeResult] = useState(null);

  const addResistor = () => {
    setResistors([...resistors, '']);
  };

  const removeResistor = (index) => {
    if (resistors.length > 2) {
      setResistors(resistors.filter((_, i) => i !== index));
    }
  };

  const updateResistor = (index, value) => {
    const newResistors = [...resistors];
    newResistors[index] = value;
    setResistors(newResistors);
  };

  const calculateSeries = () => {
    const values = resistors.map(r => parseFloat(r)).filter(v => !isNaN(v));
    if (values.length < 2) {
      toast({ title: 'Invalid Input', description: 'Please provide at least 2 resistor values.', variant: 'destructive' });
      return;
    }
    const total = values.reduce((sum, val) => sum + val, 0);
    setResult({ type: 'Series', total: total.toFixed(4), unit: '' });
    saveCalculation({ type: 'resistor-series', calculation: 'R_total = R1 + R2 + ...', inputs: { resistors: values }, result: { total } });
    toast({ title: 'Series Calculation', description: `Total Resistance = ${total.toFixed(4)} ` });
  };

  const calculateParallel = () => {
    const values = resistors.map(r => parseFloat(r)).filter(v => !isNaN(v) && v > 0);
    if (values.length < 2) {
      toast({ title: 'Invalid Input', description: 'Please provide at least 2 resistor values.', variant: 'destructive' });
      return;
    }
    const reciprocalSum = values.reduce((sum, val) => sum + (1 / val), 0);
    const total = 1 / reciprocalSum;
    setResult({ type: 'Parallel', total: total.toFixed(4), unit: '' });
    saveCalculation({ type: 'resistor-parallel', calculation: '1/R_total = 1/R1 + 1/R2 + ...', inputs: { resistors: values }, result: { total } });
    toast({ title: 'Parallel Calculation', description: `Total Resistance = ${total.toFixed(4)} ` });
  };

  const calculateColorCode = () => {
    if (!band1 || !band2 || !multiplier) {
      toast({ title: 'Invalid Input', description: 'Please select first digit, second digit, and multiplier.', variant: 'destructive' });
      return;
    }
    
    const digit1 = resistorColorCodes.find(c => c.color === band1)?.value;
    const digit2 = resistorColorCodes.find(c => c.color === band2)?.value;
    const mult = resistorColorCodes.find(c => c.color === multiplier)?.multiplier;
    const tol = tolerance ? resistorColorCodes.find(c => c.color === tolerance)?.tolerance : null;
    
    const resistance = (digit1 * 10 + digit2) * mult;
    setColorCodeResult({ resistance, tolerance: tol });
    toast({ title: 'Color Code Decoded', description: `Resistance = ${resistance}  ${tol ? ` ${tol}%` : ''}` });
  };

  const clear = () => {
    setResistors(['', '']);
    setResult(null);
  };

  const clearColorCode = () => {
    setBand1('');
    setBand2('');
    setMultiplier('');
    setTolerance('');
    setColorCodeResult(null);
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center gap-2">
          <Cpu className="h-5 w-5 text-blue-500" />
          <CardTitle>Resistor Calculator</CardTitle>
        </div>
        <CardDescription>Calculate series, parallel combinations and decode color codes</CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="combination" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="combination">Series/Parallel</TabsTrigger>
            <TabsTrigger value="colorcode">Color Code</TabsTrigger>
          </TabsList>

          <TabsContent value="combination" className="space-y-4 mt-4">
            <div className="space-y-3">
              {resistors.map((resistor, index) => (
                <div key={index} className="flex gap-2 items-end">
                  <div className="flex-1 space-y-2">
                    <Label>Resistor {index + 1} ()</Label>
                    <Input
                      type="number"
                      placeholder="Ohms"
                      value={resistor}
                      onChange={(e) => updateResistor(index, e.target.value)}
                    />
                  </div>
                  {resistors.length > 2 && (
                    <Button
                      variant="outline"
                      size="icon"
                      onClick={() => removeResistor(index)}
                      className="transition-all duration-200 hover:bg-red-50 hover:text-red-600"
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  )}
                </div>
              ))}
            </div>

            <Button variant="outline" onClick={addResistor} className="w-full">
              <Plus className="h-4 w-4 mr-2" /> Add Resistor
            </Button>

            <div className="grid grid-cols-2 gap-3">
              <Button onClick={calculateSeries} className="transition-all duration-200 hover:scale-105">Series</Button>
              <Button onClick={calculateParallel} variant="secondary" className="transition-all duration-200 hover:scale-105">Parallel</Button>
            </div>

            {result && (
              <div className="p-4 bg-gradient-to-r from-blue-50 to-cyan-50 border border-blue-200 rounded-lg">
                <p className="text-sm font-medium text-blue-900">
                  {result.type} Total: {result.total} {result.unit}
                </p>
              </div>
            )}

            <Button onClick={clear} variant="outline" className="w-full">Clear</Button>
          </TabsContent>

          <TabsContent value="colorcode" className="space-y-4 mt-4">
            <div className="space-y-4">
              <div className="space-y-2">
                <Label>First Band (1st Digit)</Label>
                <Select value={band1} onValueChange={setBand1}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select color" />
                  </SelectTrigger>
                  <SelectContent>
                    {resistorColorCodes.filter(c => c.value !== null).map(color => (
                      <SelectItem key={color.color} value={color.color}>{color.color}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label>Second Band (2nd Digit)</Label>
                <Select value={band2} onValueChange={setBand2}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select color" />
                  </SelectTrigger>
                  <SelectContent>
                    {resistorColorCodes.filter(c => c.value !== null).map(color => (
                      <SelectItem key={color.color} value={color.color}>{color.color}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label>Third Band (Multiplier)</Label>
                <Select value={multiplier} onValueChange={setMultiplier}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select color" />
                  </SelectTrigger>
                  <SelectContent>
                    {resistorColorCodes.map(color => (
                      <SelectItem key={color.color} value={color.color}>{color.color}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label>Fourth Band (Tolerance - Optional)</Label>
                <Select value={tolerance} onValueChange={setTolerance}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select color" />
                  </SelectTrigger>
                  <SelectContent>
                    {resistorColorCodes.filter(c => c.tolerance !== null).map(color => (
                      <SelectItem key={color.color} value={color.color}>{color.color} ({color.tolerance}%)</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>

            <Button onClick={calculateColorCode} className="w-full">Decode Color Code</Button>

            {colorCodeResult && (
              <div className="p-4 bg-gradient-to-r from-purple-50 to-pink-50 border border-purple-200 rounded-lg">
                <p className="text-sm font-medium text-purple-900">
                  Resistance: {colorCodeResult.resistance} 
                  {colorCodeResult.tolerance && `  ${colorCodeResult.tolerance}%`}
                </p>
              </div>
            )}

            <Button onClick={clearColorCode} variant="outline" className="w-full">Clear</Button>
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
};

export default ResistorCalculator;
