import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Button } from './ui/button';
import { Zap } from 'lucide-react';
import { toast } from '../hooks/use-toast';
import { saveCalculation } from '../mock';

const OhmsLawCalculator = () => {
  const [voltage, setVoltage] = useState('');
  const [current, setCurrent] = useState('');
  const [resistance, setResistance] = useState('');
  const [result, setResult] = useState(null);

  const calculate = () => {
    const v = parseFloat(voltage);
    const i = parseFloat(current);
    const r = parseFloat(resistance);

    let calculatedResult = null;
    let formula = '';

    // Calculate missing value
    if (!voltage && current && resistance) {
      calculatedResult = { voltage: i * r };
      formula = 'V = I  R';
      setVoltage((i * r).toFixed(4));
    } else if (voltage && !current && resistance) {
      calculatedResult = { current: v / r };
      formula = 'I = V / R';
      setCurrent((v / r).toFixed(4));
    } else if (voltage && current && !resistance) {
      calculatedResult = { resistance: v / i };
      formula = 'R = V / I';
      setResistance((v / i).toFixed(4));
    } else {
      toast({
        title: 'Invalid Input',
        description: 'Please provide exactly 2 values to calculate the third.',
        variant: 'destructive',
      });
      return;
    }

    setResult(calculatedResult);
    saveCalculation({
      type: 'ohms-law',
      calculation: formula,
      inputs: { voltage: v, current: i, resistance: r },
      result: calculatedResult,
    });

    toast({
      title: 'Calculation Complete',
      description: `${formula} = ${Object.values(calculatedResult)[0].toFixed(4)}`,
    });
  };

  const clear = () => {
    setVoltage('');
    setCurrent('');
    setResistance('');
    setResult(null);
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center gap-2">
          <Zap className="h-5 w-5 text-amber-500" />
          <CardTitle>Ohm's Law Calculator</CardTitle>
        </div>
        <CardDescription>V = I  R | Calculate voltage, current, or resistance</CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        <div className="grid gap-4 md:grid-cols-3">
          <div className="space-y-2">
            <Label htmlFor="voltage">Voltage (V)</Label>
            <Input
              id="voltage"
              type="number"
              placeholder="Volts"
              value={voltage}
              onChange={(e) => setVoltage(e.target.value)}
              className="transition-all duration-200 focus:ring-2 focus:ring-amber-500"
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="current">Current (I)</Label>
            <Input
              id="current"
              type="number"
              placeholder="Amperes"
              value={current}
              onChange={(e) => setCurrent(e.target.value)}
              className="transition-all duration-200 focus:ring-2 focus:ring-blue-500"
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="resistance">Resistance (R)</Label>
            <Input
              id="resistance"
              type="number"
              placeholder="Ohms"
              value={resistance}
              onChange={(e) => setResistance(e.target.value)}
              className="transition-all duration-200 focus:ring-2 focus:ring-green-500"
            />
          </div>
        </div>

        {result && (
          <div className="p-4 bg-gradient-to-r from-green-50 to-emerald-50 border border-green-200 rounded-lg">
            <p className="text-sm font-medium text-green-900">
              Result: {Object.keys(result)[0]} = {Object.values(result)[0].toFixed(4)}
            </p>
          </div>
        )}

        <div className="flex gap-3">
          <Button onClick={calculate} className="flex-1 transition-all duration-200 hover:scale-105">
            Calculate
          </Button>
          <Button onClick={clear} variant="outline" className="transition-all duration-200 hover:scale-105">
            Clear
          </Button>
        </div>
      </CardContent>
    </Card>
  );
};

export default OhmsLawCalculator;
