import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Button } from './ui/button';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './ui/tabs';
import { Zap } from 'lucide-react';
import { toast } from '../hooks/use-toast';
import { saveCalculation } from '../mock';

const PowerCalculator = () => {
  const [voltage, setVoltage] = useState('');
  const [current, setCurrent] = useState('');
  const [resistance, setResistance] = useState('');
  const [power, setPower] = useState('');
  const [result, setResult] = useState(null);

  const calculatePVI = () => {
    const v = parseFloat(voltage);
    const i = parseFloat(current);

    if (!v || !i) {
      toast({
        title: 'Invalid Input',
        description: 'Please provide both voltage and current.',
        variant: 'destructive',
      });
      return;
    }

    const p = v * i;
    setPower(p.toFixed(4));
    setResult({ power: p, formula: 'P = V  I' });
    saveCalculation({
      type: 'power',
      calculation: 'P = V  I',
      inputs: { voltage: v, current: i },
      result: { power: p },
    });
    toast({ title: 'Calculation Complete', description: `Power = ${p.toFixed(4)} W` });
  };

  const calculatePI2R = () => {
    const i = parseFloat(current);
    const r = parseFloat(resistance);

    if (!i || !r) {
      toast({
        title: 'Invalid Input',
        description: 'Please provide both current and resistance.',
        variant: 'destructive',
      });
      return;
    }

    const p = i * i * r;
    setPower(p.toFixed(4));
    setResult({ power: p, formula: 'P = I  R' });
    saveCalculation({
      type: 'power',
      calculation: 'P = I  R',
      inputs: { current: i, resistance: r },
      result: { power: p },
    });
    toast({ title: 'Calculation Complete', description: `Power = ${p.toFixed(4)} W` });
  };

  const calculatePV2R = () => {
    const v = parseFloat(voltage);
    const r = parseFloat(resistance);

    if (!v || !r) {
      toast({
        title: 'Invalid Input',
        description: 'Please provide both voltage and resistance.',
        variant: 'destructive',
      });
      return;
    }

    const p = (v * v) / r;
    setPower(p.toFixed(4));
    setResult({ power: p, formula: 'P = V / R' });
    saveCalculation({
      type: 'power',
      calculation: 'P = V / R',
      inputs: { voltage: v, resistance: r },
      result: { power: p },
    });
    toast({ title: 'Calculation Complete', description: `Power = ${p.toFixed(4)} W` });
  };

  const clear = () => {
    setVoltage('');
    setCurrent('');
    setResistance('');
    setPower('');
    setResult(null);
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center gap-2">
          <Zap className="h-5 w-5 text-orange-500" />
          <CardTitle>Power Calculator</CardTitle>
        </div>
        <CardDescription>Calculate electrical power using different formulas</CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="pvi" className="w-full">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="pvi">P = V  I</TabsTrigger>
            <TabsTrigger value="pi2r">P = I  R</TabsTrigger>
            <TabsTrigger value="pv2r">P = V / R</TabsTrigger>
          </TabsList>

          <TabsContent value="pvi" className="space-y-4 mt-4">
            <div className="grid gap-4 md:grid-cols-2">
              <div className="space-y-2">
                <Label htmlFor="voltage-pvi">Voltage (V)</Label>
                <Input
                  id="voltage-pvi"
                  type="number"
                  placeholder="Volts"
                  value={voltage}
                  onChange={(e) => setVoltage(e.target.value)}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="current-pvi">Current (I)</Label>
                <Input
                  id="current-pvi"
                  type="number"
                  placeholder="Amperes"
                  value={current}
                  onChange={(e) => setCurrent(e.target.value)}
                />
              </div>
            </div>
            <Button onClick={calculatePVI} className="w-full">Calculate Power</Button>
          </TabsContent>

          <TabsContent value="pi2r" className="space-y-4 mt-4">
            <div className="grid gap-4 md:grid-cols-2">
              <div className="space-y-2">
                <Label htmlFor="current-pi2r">Current (I)</Label>
                <Input
                  id="current-pi2r"
                  type="number"
                  placeholder="Amperes"
                  value={current}
                  onChange={(e) => setCurrent(e.target.value)}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="resistance-pi2r">Resistance (R)</Label>
                <Input
                  id="resistance-pi2r"
                  type="number"
                  placeholder="Ohms"
                  value={resistance}
                  onChange={(e) => setResistance(e.target.value)}
                />
              </div>
            </div>
            <Button onClick={calculatePI2R} className="w-full">Calculate Power</Button>
          </TabsContent>

          <TabsContent value="pv2r" className="space-y-4 mt-4">
            <div className="grid gap-4 md:grid-cols-2">
              <div className="space-y-2">
                <Label htmlFor="voltage-pv2r">Voltage (V)</Label>
                <Input
                  id="voltage-pv2r"
                  type="number"
                  placeholder="Volts"
                  value={voltage}
                  onChange={(e) => setVoltage(e.target.value)}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="resistance-pv2r">Resistance (R)</Label>
                <Input
                  id="resistance-pv2r"
                  type="number"
                  placeholder="Ohms"
                  value={resistance}
                  onChange={(e) => setResistance(e.target.value)}
                />
              </div>
            </div>
            <Button onClick={calculatePV2R} className="w-full">Calculate Power</Button>
          </TabsContent>
        </Tabs>

        {result && (
          <div className="mt-4 p-4 bg-gradient-to-r from-orange-50 to-amber-50 border border-orange-200 rounded-lg">
            <p className="text-sm font-medium text-orange-900">
              {result.formula} = {result.power.toFixed(4)} W
            </p>
          </div>
        )}

        <Button onClick={clear} variant="outline" className="w-full mt-4">Clear</Button>
      </CardContent>
    </Card>
  );
};

export default PowerCalculator;
