import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Button } from './ui/button';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './ui/tabs';
import { Battery } from 'lucide-react';
import { toast } from '../hooks/use-toast';
import { saveCalculation } from '../mock';

const BatteryCalculator = () => {
  // Runtime calculator
  const [capacity, setCapacity] = useState('');
  const [current, setCurrent] = useState('');
  const [runtimeResult, setRuntimeResult] = useState(null);

  // Series/Parallel calculator
  const [batteryVoltage, setBatteryVoltage] = useState('');
  const [batteryCapacity, setBatteryCapacity] = useState('');
  const [seriesCount, setSeriesCount] = useState('');
  const [parallelCount, setParallelCount] = useState('');
  const [configResult, setConfigResult] = useState(null);

  const calculateRuntime = () => {
    const cap = parseFloat(capacity);
    const cur = parseFloat(current);

    if (!cap || !cur || cur <= 0) {
      toast({ title: 'Invalid Input', description: 'Please provide valid capacity and current values.', variant: 'destructive' });
      return;
    }

    const hours = cap / cur;
    const minutes = hours * 60;
    
    setRuntimeResult({ hours: hours.toFixed(2), minutes: minutes.toFixed(0) });
    saveCalculation({
      type: 'battery-runtime',
      calculation: 'Runtime = Capacity / Current',
      inputs: { capacity: cap, current: cur },
      result: { runtime_hours: hours },
    });
    toast({ title: 'Runtime Calculated', description: `Battery will last ${hours.toFixed(2)} hours` });
  };

  const calculateConfiguration = () => {
    const v = parseFloat(batteryVoltage);
    const cap = parseFloat(batteryCapacity);
    const series = parseInt(seriesCount) || 1;
    const parallel = parseInt(parallelCount) || 1;

    if (!v || !cap || series < 1 || parallel < 1) {
      toast({ title: 'Invalid Input', description: 'Please provide valid battery specifications.', variant: 'destructive' });
      return;
    }

    const totalVoltage = v * series;
    const totalCapacity = cap * parallel;
    const totalBatteries = series * parallel;

    setConfigResult({
      voltage: totalVoltage.toFixed(2),
      capacity: totalCapacity.toFixed(2),
      batteries: totalBatteries,
    });

    saveCalculation({
      type: 'battery-config',
      calculation: `${series}S${parallel}P Configuration`,
      inputs: { voltage: v, capacity: cap, series, parallel },
      result: { total_voltage: totalVoltage, total_capacity: totalCapacity },
    });

    toast({ 
      title: 'Configuration Calculated', 
      description: `${totalVoltage.toFixed(2)}V with ${totalCapacity.toFixed(2)}Ah capacity` 
    });
  };

  const clearRuntime = () => {
    setCapacity('');
    setCurrent('');
    setRuntimeResult(null);
  };

  const clearConfig = () => {
    setBatteryVoltage('');
    setBatteryCapacity('');
    setSeriesCount('');
    setParallelCount('');
    setConfigResult(null);
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center gap-2">
          <Battery className="h-5 w-5 text-green-500" />
          <CardTitle>Battery Calculator</CardTitle>
        </div>
        <CardDescription>Calculate runtime and series/parallel configurations</CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="runtime" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="runtime">Runtime</TabsTrigger>
            <TabsTrigger value="config">Configuration</TabsTrigger>
          </TabsList>

          <TabsContent value="runtime" className="space-y-4 mt-4">
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="capacity">Battery Capacity (mAh or Ah)</Label>
                <Input
                  id="capacity"
                  type="number"
                  placeholder="e.g., 2000 mAh"
                  value={capacity}
                  onChange={(e) => setCapacity(e.target.value)}
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="current-draw">Current Draw (mA or A)</Label>
                <Input
                  id="current-draw"
                  type="number"
                  placeholder="e.g., 500 mA"
                  value={current}
                  onChange={(e) => setCurrent(e.target.value)}
                />
              </div>
            </div>

            <Button onClick={calculateRuntime} className="w-full">Calculate Runtime</Button>

            {runtimeResult && (
              <div className="p-4 bg-gradient-to-r from-green-50 to-emerald-50 border border-green-200 rounded-lg">
                <p className="text-sm font-medium text-green-900">
                  Runtime: {runtimeResult.hours} hours ({runtimeResult.minutes} minutes)
                </p>
                <p className="text-xs text-green-700 mt-1">
                  Note: Actual runtime may vary based on discharge rate and battery condition
                </p>
              </div>
            )}

            <Button onClick={clearRuntime} variant="outline" className="w-full">Clear</Button>
          </TabsContent>

          <TabsContent value="config" className="space-y-4 mt-4">
            <div className="space-y-4">
              <div className="grid gap-4 md:grid-cols-2">
                <div className="space-y-2">
                  <Label htmlFor="battery-voltage">Single Battery Voltage (V)</Label>
                  <Input
                    id="battery-voltage"
                    type="number"
                    placeholder="e.g., 3.7"
                    value={batteryVoltage}
                    onChange={(e) => setBatteryVoltage(e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="battery-capacity">Single Battery Capacity (mAh)</Label>
                  <Input
                    id="battery-capacity"
                    type="number"
                    placeholder="e.g., 2000"
                    value={batteryCapacity}
                    onChange={(e) => setBatteryCapacity(e.target.value)}
                  />
                </div>
              </div>

              <div className="grid gap-4 md:grid-cols-2">
                <div className="space-y-2">
                  <Label htmlFor="series">Batteries in Series (S)</Label>
                  <Input
                    id="series"
                    type="number"
                    placeholder="e.g., 2"
                    value={seriesCount}
                    onChange={(e) => setSeriesCount(e.target.value)}
                  />
                  <p className="text-xs text-muted-foreground">Increases voltage</p>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="parallel">Batteries in Parallel (P)</Label>
                  <Input
                    id="parallel"
                    type="number"
                    placeholder="e.g., 2"
                    value={parallelCount}
                    onChange={(e) => setParallelCount(e.target.value)}
                  />
                  <p className="text-xs text-muted-foreground">Increases capacity</p>
                </div>
              </div>
            </div>

            <Button onClick={calculateConfiguration} className="w-full">Calculate Configuration</Button>

            {configResult && (
              <div className="p-4 bg-gradient-to-r from-green-50 to-emerald-50 border border-green-200 rounded-lg space-y-2">
                <p className="text-sm font-medium text-green-900">
                  Configuration: {seriesCount}S{parallelCount}P ({configResult.batteries} batteries total)
                </p>
                <p className="text-sm text-green-800">
                  Total Voltage: {configResult.voltage} V
                </p>
                <p className="text-sm text-green-800">
                  Total Capacity: {configResult.capacity} mAh
                </p>
              </div>
            )}

            <Button onClick={clearConfig} variant="outline" className="w-full">Clear</Button>
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
};

export default BatteryCalculator;
